.container {
  display: grid;
  grid-template-rows: 60px 1fr 60px;
  grid-template-columns: 200px 1fr;
  grid-template-areas:
  "h h"
  "s c"
  "f f";
  min-height: 100vh;
  height: 100%;
}

.content {
  grid-area: c;
  padding: 40px 100px 40px 100px;
  background-color: #ffffff;
  font-family: 'Nunito Sans', sans-serif;
}
